<template>
  <div class="container">
    <div class="search-wrapper">
      <span class="icon icon-search"></span>
      <v-select
        v-model="e7"
        :items="states"
        multiple
        small-chips
        flat
        solo
        color="#0062FF"
        class="search-select"
        ><template #selection="{ item }">
          <v-chip
            outlined
            color="primary"
            close
            @click:close="deleteChip(item, e7)"
            >{{ item }}</v-chip
          >
        </template></v-select
      >
      <button class="search-wrapper-btn">Search</button>
    </div>
    <v-divider></v-divider>
  </div>
</template>

<script>
export default {
  name: 'SearchComponent',
  data() {
    return {
      e7: ['javaScript', 'HTML', 'CSS', 'SCSS'],
      states: [
        'javaScript',
        'Python',
        'Java',
        'Ruby',
        'HTML',
        'CSS',
        'SCSS',
        'SQL',
        'MYSQL',
        'postgressql',
        'FRONTENT',
        'Backend',
        'Django',
        'PHP',
        'React',
        'Vue',
        'jquery',
        'vuetify',
      ],
    };
  },
  methods: {
    deleteChip(itemNeedToRemove, array) {
      for (let i = 0; i < array.length; i += 1) {
        if (array[parseInt(i, 10)] === itemNeedToRemove) {
          array.splice(i, 1);
        }
      }
    },
  },
};
</script>

<style src="./SearchComponent.scss" lang="scss"></style>
